import { Link} from 'react-router-dom';
import React from 'react';
import DataList from '../data/workList.json';
import Header from '../comp/Header';
import Footer from '../comp/Footer';


export default function Work(){

    const workList = DataList.List.map((item,key)=>(
        <Link to={`/Detail/:${key}`}>
            <li key ={key} id={key} onLoad={alarm}>
                <img src={item.bg} alt=""></img>

                <div className="con">
                    <div className="con_tit">{item.detial_sub}</div>
                    <p>{item.detail_tit}</p>
                    <div className="skill">
                        <span>{item.detail_skill}</span>
                        <span>{item.detail_skill}</span>
                    </div>
                </div>
            </li>
        </Link>
    ));  

    function alarm() {
        const final = document.querySelectorAll('.work ul li');

        final[5].onclick = function (e) {
            e.preventDefault()
            alert('í•´ë‹¹ í˜ì´ì§€ëŠ” ì¤€ë¹„ì¤‘ì…ë‹ˆë‹¤.')
        }
    }

    return(
        <div>
        <Header/>

        <div className="work">
            <ul>
                {workList}
            </ul>
            
        </div>

        <Footer/>
        </div>
    )
}