import {Hill} from './Hill';

export default class Bgcanvas {
    
    constructor(canvasElement){
        
        this.canvas = canvasElement;
        this.ctx = canvasElement.getContext('2d');

        // ìº”ë²„ìŠ¤ ì¶”ê°€í•´ì„œ bodyì— ì¶”ê°€
        this.toMeridium = new Date();
        this.hours = this.toMeridium.getHours() < 21 ? "day":"night";
        (this.hours == 'day') ? this.check(true) : this.check(false);
    }

    check(checkToggle){
       
            if(this.hours == 'night' || checkToggle){
            this.canvas.classList = 'day_bg';
            
            document.querySelector('.moon').style.display = 'none';
            document.querySelector('.balloon').style.display = 'block';
            document.querySelector('.star').style.display='none';
            document.querySelector('.cloud-container').style.display='block';
            document.querySelector('.main_title').style.color='#111';
            document.querySelector('.develop').style.color='#111';

            this.hills = [
                new Hill('#333311', 0.2, 12),
                new Hill('#6D7441', 0.5, 8),
                new Hill('#97A26A', 0.8, 6),
                new Hill('#B5C855', 1.1, 4),
                new Hill('#C6CEA7', 1.3, 2),
            ];
        }else{
            this.canvas.classList = 'night_bg';

            document.querySelector('.star').style.display='block';
            document.querySelector('.moon').style.display = 'block';
            document.querySelector('.balloon').style.display = 'none';
            document.querySelector('.cloud-container').style.display='none';
            document.querySelector('.main_title').style.color='#fff';
            document.querySelector('.develop').style.color='#fff';

            this.hills = [
                new Hill('#301F43', 0.2, 12),
                new Hill('#5E278D', 0.5, 8),
                new Hill('#5E339E', 0.8, 6),
                new Hill('#6837A4', 1.1, 4),
                new Hill('#CE9DEF', 1.3, 2),
            ];
        }

        window.addEventListener('resize',this.resize.bind(this),false); //?
        this.resize();
        //ìŠ¤í¬ë¦° ì‚¬ì´ì¦ˆë¥¼ ê°€ì ¸ì˜¤ê¸° ìœ„í•´ì„œ resizeì´ë²¤íŠ¸ë¥¼ ê±¸ì–´ì¤Œ

        requestAnimationFrame(this.animate.bind(this));
    }

    resize(){
        this.stageWidth = document.body.clientWidth; //?
        this.stageHeight = document.body.clientHeight;

        this.canvas.width = this.stageWidth * 2;
        this.canvas.height = this.stageHeight * 2;
        this.ctx.scale(2,2); 
        //ìº”ë²„ìŠ¤ ì‚¬ì´ì¦ˆë¥¼ í‚¤ì›Œì„œ ë ˆí‹°ë‚˜ ë””ìŠ¤í”Œë ˆì´ì—ë„ ì ìš©

        for(let i=0; i<this.hills.length; i++){
            this.hills[i].resize(this.stageWidth, this.stageHeight);
        }
    }

    animate(t){
        requestAnimationFrame(this.animate.bind(this));

        this.ctx.clearRect(0,0,this.stageWidth,this.stageHeight);
        //ìº”ë²„ìŠ¤ë¥¼ ì§€ì›Œì£¼ëŠ” ì½”ë“œ

        let dots;
        for (let i=0; i<this.hills.length; i++){
            dots = this.hills[i].draw(this.ctx);
        }
    }
}