import '../App.css';
import Canvas from './Canvas';
import React from 'react'
import Confetti from 'react-confetti'
import { render } from '@testing-library/react';
import {useState} from 'react';

export default function Bg({a}){
    //ì˜¤ì „+ì˜¤í›„
    let [hours,setHours] = useState();
    function headProps(st){
        setHours(st);
    }

    a(hours);
    

    //í­ì£½ ë¼ì´ë¸ŒëŸ¬ë¦¬ ì‚¬ìš©
    function pop(){
        render(
            <Confetti
                width={window.innerWidth}
                height={window.innerHeight}
                recycle={false}
            />
        )
    }

    return(
        <div>
            <Canvas d={headProps}/>
            <div className = "balloon" onClick={pop}></div>
            <div className = "moon" onClick={pop}></div>
        </div>
    )
}